@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Raleway&display=swap");
* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

html::-webkit-scrollbar {
  width: 0.5rem;
}

html::-webkit-scrollbar-track {
  background: transparent;
}

html::-webkit-scrollbar-thumb {
  background: #00adb5;
  border-radius: 5rem;
}
body {
  width: 100%;
  overflow-x: hidden;
}
main {
  width: 100%;
  overflow-x: hidden;
}
.Motto {
  background-image: url(.//img/Motto.jpg);
}
.Motto::after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(
    to right,
    #00adb5 0%,
    rgba(0, 173, 181, 0.11) 70.76%,
    rgba(255, 255, 255, 0) 100%
  );
}
h1,
h2,
h4,
.SpanTitle {
  font-family: "Raleway", sans-serif;
}

#serviceArticle::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background: #00adb5;
  border-radius: 20px;
  scale: 0;
  transition: 0.5s ease-out;
}

#serviceArticle:hover::after {
  scale: 1;
}

#serviceArticle:hover h4 {
  color: #fff;
}
#serviceArticle:hover p {
  color: #fff;
}
#serviceArticle:hover a {
  color: #fff;
}
#serviceArticle a:hover > span {
  margin-left: 1.2rem;
}
.overlay {
  background-image: linear-gradient(
    to right,
    #00acb577 0%,
    rgba(0, 173, 181, 0.11) 70.76%,
    rgba(255, 255, 255, 0.788) 100%
  );
}
.sub {
  background-image: url(./img/Subscribe.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-color: #1e1e1e6d;
  background-blend-mode: multiply;
  overflow-x: hidden;
}
#BlogArticle a:hover > span {
  margin-left: 1.2rem;
}
#BlogArticle:hover figure > img {
  transform: scale(1.1);
}
footer {
  background: url(../src/img/footer-bg.jpg) no-repeat;
  background-size: 100% 100%;
}
.RoundImg {
  border-radius: 20px 20px 0 0;
}
.PaddingDoctor {
  padding: 15px 15px 30px;
}
.BorderSpan {
  border: 2px solid #fff;
}
